import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from './useAuth';
import { toast } from './use-toast';

/**
 * Hook pour exiger un rle spcifique
 * Redirige automatiquement si l'utilisateur n'a pas le rle requis
 */
export const useRequireRole = (role: string, redirectTo = '/') => {
  const { hasRole, loading } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    if (!loading && !hasRole(role)) {
      toast({
        title: "Accs refus",
        description: `Vous devez avoir le rle '${role}' pour accder  cette page.`,
        variant: "destructive",
      });
      navigate(redirectTo);
    }
  }, [hasRole, loading, role, navigate, redirectTo]);

  return { hasRole: hasRole(role), loading };
};
